eval("define(\'app\',[\'angular\', \'config/config\', \'angular-route\', \'bss/bss\', \'animate\', \'menuPrincipal/menuPrincipal\', \'cabecera/cabecera\', \'buscador/buscador\', \'translations\'], function(angular, config) {\r\n    \"use strict\";\r\n\r\n\r\n    var app = angular.module(\"app\", config.standardAngularModules);\r\n\r\n    app.config([\'$httpProvider\', \'$routeProvider\', function($httpProvider, $routeProvider) {\r\n                var httpLogInterceptor;\r\n\r\n                // $httpProvider.defaults.withCredentials = true;\r\n\r\n                $routeProvider.otherwise({\r\n                    redirectTo: \'/menuPrincipal/\'\r\n                });\r\n\r\n                httpLogInterceptor = [\r\n                        \'$q\', function($q) {\r\n\r\n                            function success(response) {\r\n                                console.log(\"Successful HTTP request. Response:\", response);\r\n                                return response;\r\n                            }\r\n\r\n                            function error(response) {\r\n                                console.log(\"Error in HTTP request. Response:\", response);\r\n\r\n                                return $q.reject(response);\r\n                            }\r\n\r\n                            return function(promise) {\r\n                                return promise.then(success, error);\r\n                            };\r\n                        }\r\n                ];\r\n\r\n                $httpProvider.responseInterceptors.push(httpLogInterceptor);\r\n            }\r\n    ]);\r\n\r\n    app.run(function(gettextCatalog) {\r\n        gettextCatalog.setCurrentLanguage(\'es\');\r\n        gettextCatalog.debug = true;\r\n    });\r\n\r\n    return app;\r\n});\r\n\n//# sourceURL=/app.js");

